import React, { useState, useEffect } from 'react';
// import { useHistory } from 'react-router-dom';

import '../../css/style.css';
// import useGlobalState from '../../hooks/useGlobalState';
// import useGeolocation from 'react-hook-geolocation'


const display = {
    display: 'block'
};
const hide = {
    display: 'none'
};




const POACapture = () => {

    const [showDialog, setShowDialog] = useState(false)
    const [loading, setLoading] = useState(false)
    const [img, setImg] = useState(false);


    useEffect(() => {

        window.idSDK.init();

    }, [])

    const myCallBackFunction = (response) => {
        if (response.success) {
            let image = response.data.image;
            setImg(image);
            console.log("image : ", image)
            console.log("response : ", response)
        } else {
            alert(response.message)
        }
    }

    const capturePOA = () => {
        window.idSDK.captureDocumentBack(false, myCallBackFunction);
    }



    return (
        <div>
            <div style={{ textAlign: "center" }}>



                <p style={{ color: "black", "fontWeight": "bolder" }}>Capture Back View</p>


                <div id="poaview" class="photoPreviewFrame">

                    <button style={{ "padding": "20px" }} 
                    onClick = {(e) => capturePOA()}
                    // onClick={child.fetchLocation.bind(this, "Front Side")}
                    >
                        <img id="FrontImage" height="100" width="100" src={require("../../img/poi.png")} alt="If POA is same as POI Click back side."></img>
                    </button>

                    <div class="col-6 col-sm-6">
                        <button type="submit" class="btn-block jio-btn jio-btn-primary" 
                        // onClick={child.previewClicked.bind(this, "FRONT")} 
                        >Preview</button>

                    </div>

                </div>



                <div class="bottom-fixed-btn">
                    <p class="mt-10" style={{ color: "red", "fontWeight": "bolder" }}>Ensure camera to complete auto focus for image capture</p>

                    <div class="row m-0 mt-4">
                        <div class="col-12 p-2">
                            <button type="button" 
                            // onClick={child.validateAndNext.bind(this)} 
                            class="btn-block jio-btn jio-btn-primary">NEXT</button>
                        </div>
                    </div>
                </div>




            </div>


        </div>
    )
}

export default POACapture